import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the TbarPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tbar',
  templateUrl: 'tbar.html',
})
export class TbarPage {

   @ViewChild('mySlider') mySlider: any;
_imageViewerCtrl: ImageViewerController;
  constructor(public navCtrl: NavController, public navParams: NavParams, imageViewerCtrl: ImageViewerController, public platform: Platform) {
  this._imageViewerCtrl = imageViewerCtrl;
  let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
      
      
  }
  
   presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }
  
  elenco =  [
             {
               "img": "assets/img/qaprofil/tbar/1.png",
               "posicao": "Main T - Bar",
               "nome": "QSGTBMT32-360"
               
             },
             {
               "img": "assets/img/qaprofil/tbar/2.png",
                  "posicao": "Cross T - Bar",
               "nome": "QSGTBCT24-120 / QSGTBCT24-60"
               
             },
             {
               "img": "assets/img/qaprofil/tbar/3.png",
                  "posicao": "",
               "nome": ""
               
             },
             {
               "img": "assets/img/qaprofil/tbar/4.png",
                  "posicao": "",
               "nome": ""
               
             },
             {
               "img": "assets/img/qaprofil/full_image/tbar-1.jpg",
                "posicao": "",
               "nome": ""
             } 
             
           ]
           
           
           slideNext(){
       this.mySlider.slideNext();
     }
 
     slidePrev(){
       this.mySlider.slidePrev();
     }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TbarPage');
  }
  
      startApp(page) {
    this.navCtrl.setRoot(page, {}, {
      animate: true,
      direction: 'forward'
    });
  
}

}



